.SuperCheckbox_checkbox__3rHyo[type="checkbox"] {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    width: 40px;
    height: 16px;
    border: 1px solid #aaa;
    border-radius: 2px;
    background: #ebebeb;
    position: relative;
    display: inline-block;
    overflow: hidden;
    vertical-align: middle;
    transition: background 0.3s;
    box-sizing: border-box;
}

.SuperCheckbox_checkbox__3rHyo:after {
    content: '';
    position: absolute;
    top: -1px;
    left: -1px;
    width: 14px;
    height: 14px;
    background: white;
    border: 1px solid #aaa;
    border-radius: 2px;
    transition: left 0.1s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.SuperCheckbox_checkbox__3rHyo:checked {
    background: #2ecc71;
    border-color: #2ecc71;
}

.SuperCheckbox_checkbox__3rHyo:checked:after {
    left: 23px;
    border-color: #2ecc71;
}

.SuperCheckbox_checkbox__3rHyo:hover:not(:checked):not(:disabled):after,
.SuperCheckbox_checkbox__3rHyo:focus:not(:checked):not(:disabled):after {
    left: 0;
}

.SuperCheckbox_checkbox__3rHyo:hover:checked:not(:disabled):after,
.SuperCheckbox_checkbox__3rHyo:focus:checked:not(:disabled):after {
    left: 22px;
}

.SuperCheckbox_checkbox__3rHyo:disabled {
    opacity: 0.5;
}

